.defectList {
  background-color: #0746f54d;
}

.defectList_gridHeader {
  display: grid;
  grid-template-columns: 3fr 5fr 8fr 1.5fr 1.5fr 1fr 1rem;
  grid-template-rows: 3rem;
  column-gap: 1rem;
  font-weight: bold;
  padding-left: 5px;
  align-items: center;
}

.defectList_gridDetail {
  display: grid;
  grid-template-columns: 3fr 5fr 8fr 1.5fr 1.5fr 1fr 1rem;
  grid-template-rows: 3rem;
  column-gap: 1rem;
  border-top: 1px solid white;
  padding-left: 5px;
  align-items: center;
  
}

.defectList_gridItem {
  display: flex;
  align-items: center;
  min-width: 0;
}

.defectList_gridItem > span {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.defectList_gridItem > span:hover {
  overflow-x: visible;
  text-overflow: inherit;
}

.defectList_major,
.defectList_major_header {
  justify-self: center;
}

.defectList_minor,
.defectList_minor_header {
  justify-self: center;
}

.defectList_deleteButton {
  margin-left: 0.7rem;
}

.defectList_highLight {
  color: red;
}
