.misc {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 2rem 2rem 2rem 2rem 2rem 2rem 2rem 2rem 2rem 3rem 6rem 4rem;
  grid-auto-rows: 2rem;
  background-color: #0746f54d;
  align-items: center;
  justify-content: left;
  margin-top: 5px;
  padding: 10px;
  font-size: 1rem;
  grid-template-areas:
    "g a"   
    "t o"   
    "u p"     
    "q ."    
    "j r"   
    "k c"   
    "b d"   
    "s h"    
    "n i"   
    "e e"   
    "f f"   
    "l m"
  
}
