.misc_title {
  font-weight: bold;
}

.misc input,
select {
  border-width: 1px;
  border-color: gray;
  background-color: white;
  padding: 0px;
  margin: 0px;
  font-size: 1rem;
}

.misc input[type="radio"]:checked {
  box-shadow: 0 0 0 3px blue;
}

.misc textarea {
  margin-top: 5px;
  resize: none;
  border-width: 1px;
  border-color: gray;
  background-color: white;
  padding: 1px;
  font-size: 1rem;
}

.misc .datePicker {
  font-size: 1.2rem;
  text-align: left;
  
}

#misc_sqa {
  padding-left: 3px;
  width: 15rem;
}

#misc_mqa {
  width: 15rem;
}

#misc_pack_type {
  width: 17.5rem;
}


.misc_protect { 
  margin-right:10px;
  background: repeating-linear-gradient(
  -45deg,
  #606dbc,
  #606dbc 10px,
  #465298 10px,
  #465298 20px
);
 
}

