.photoBlock img {
  border-radius: 4px; /* Rounded border */
  padding: 1px; /* Some padding */
  width: 50px; /* Set a small width */
  height: 50px; /* Set a small width */
  margin-left: 10px;
}

.photoBlock :hover {
  transform: scale(7);
  position: relative;
  left: 210px;
  border-color: blue;
  border-width: 1px;
  padding: 0px;
}

.photoBlock {
  display: flexbox;
  margin: 1px;
  padding-right: 10px;
  background-color: #0746f54d;
}

.photoBorder {
  padding: 0px;
  /* border: 1px solid blue;   */
}
