.searchKey {
  display: grid;
  grid-template-columns: 1fr 1fr 2fr 0.35fr 1fr;
  grid-template-rows: 3rem;
  align-items: center;
  color: black;
  background-color: rgb(92, 152, 241);
}

.searchKey_pair {
  display: flex;
}

.searchKey_pair:nth-child(1) {
  margin-left: 0.9rem;
}

.searchKey label {
  margin: 0px;
  padding: 0px;
  text-align: right;
  margin-right: 5px;
}

.searchKey input,
select {
  margin: 0px;
  padding: 5px;
  border-width: 1px;
  border-color: gray;
  background-color: white;
  width: 8rem;
  text-align: left;
}

#searchKey_type {
  padding-left: 5px;
  width: 13rem;
}

#searchKey_iteration {
  text-align: center;
  width: 6rem;
}

.searchKey input:hover {
  border-width: 1px;
  border-color: red;
}

.searchKey_alert {
  color: white;
  background-color: blueviolet;
}

.searchKey .fa-plus {
  color: blue;
}

.searchKey .fa-plus:hover {
  color: blue;
  font-size: 30px;
}

.searchKey button {
  font-size: 0.9rem;
  margin-left: -4.8rem;
  margin-top: 0.5rem;
  width: 10rem;
}
