.result {
  display: grid;
  grid-template-columns: 1.5fr 1.8fr 1.7fr;
  grid-template-rows: 2rem 2rem 2rem;  
  grid-auto-rows:2rem;  
  row-gap: 0.3rem;
  border: 4px solid orange;
  color:white;
  background-color:rgb(42, 44, 70);
  align-items: center;
  justify-content: left;
  margin-top: 0.35rem;
  height: 9rem;
  padding: 15px;  
  grid-template-areas:  
    "f . . "
    "a d c"
    "b g e"

  }

.result_a {    
  grid-area: a;    
}

.reult_b {
  grid-area: b;
}

.result_c {
  grid-area: c;
}

.result_d {
  grid-area: d;  
}

.result_e {
  border: white 1px solid;  
  grid-area: e;
}

.result_f {
  font-size:1.2rem;
  font-weight: bold;
  grid-area: f;
}

.result_g {  
  grid-area: g;
}


.result_grid_a {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
}

.result_grid_b {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
}

.result_grid_c {
  display: grid;
  grid-template-columns:  2fr  1fr 1fr;
  align-items: center;
}

.result_grid_d {
  display: grid;
  grid-template-columns: 3fr  1fr 0.5fr;
  align-items: center;  
}

.result_grid_e {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr;
  align-items: center;
}

.result_grid_f {
  display: grid;
  grid-template-columns: 1fr ;
  align-items: center;
}

.result_grid_g {
  display: grid;
  grid-template-columns: 3fr  1fr 0.5fr;
  align-items: center;      
}


.result_title {
  font-weight: bold;
}

.result input, select {
  border-width: 1px;
  border-color: gray;
  background-color: white;
  padding: 0px;
  margin: 0px;
  font-size: 1rem;
  align-items: center;
  padding-left: 3px;
}

