.userProfile {
  margin: 0;
  padding: 5px;
  display: flex;
  height: 70px;
  align-items: center;
  justify-content: space-between;
  color: white;  
  margin-bottom: 0.1rem;
}

.userProfile_prod_color {
    background-color: rgba(1, 103, 241, 255);
}

.userProfile_dev_color {
  background-color: red;
}

.userProfile_title {
  display: grid;
  text-align: left;
  grid-template-columns: 1.5fr 10fr;
  color: white;
  font-style: italic;
  font-size: 30px;
}

.userProfile_title:nth-child(1) {
  text-align: center;
}

.userProfile_title img {
  margin-top: 0.5rem;
}

.userProfile_personal {
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.userProfile_name {
  font-size: 20px;
}

.userProfile_photo {
  margin-top: 5px;
  margin-bottom: 5px;
  padding-top: 5px;
  margin-left: 20px;
}

.userProfile_photo :hover {
  margin-top: 11rem;
  margin-right: 5.3rem;
  transform: scale(4);
}
