.mainMenu {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  background-color: rgb(92, 152, 241);
  max-height: 5rem;
}

.mainMenu button {
  padding: 0.5rem;
  margin-left: 0.7rem;
  margin-right: 0.2rem;
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  width: 6rem;
}

.mainMenu .deleteButton,
.mainMenu .submitButton {
  background-color: rgb(245, 92, 92);
}

.mainMenu .deleteButton:hover,
.mainMenu .submitButton:hover {
  background-color: rgb(241, 10, 10);
}

.mainMenu_lock {
  font-size: 1.5rem;
  color: red;
}

.mainMenu_unlock {
  font-size: 1.5rem;
  color: green;
}

.mainMenu_inspectionKey {
  font-size: 1.2rem;
  margin-left: 0.8rem;
  margin-right: auto;
  font-weight: bold;
  color: white;
}
