.changeTrack_root {
  --changeTrack_max_height: "4.3rem";
  --changeTrack_scroll_width: "6.7fr";
}

.changeTrack_section_upper {
  margin-top: 1rem;
  font-weight: bold;
  display: grid;
  grid-template-columns: 1fr 0.9fr 1fr 0.06fr 2fr 1.24fr;
}

.changeTrack_section_bottom {
  margin-top: 0rem;
  border: 2px solid blue;
}

.changeTrack_grid_header {
  display: grid;
  grid-template-columns: 4.6fr 2.5fr 1fr 7fr;
  column-gap: 1rem;
  justify-content: center;
  font-size: 0.7rem;
  color: black;
  align-items: center;
  font-weight: bold;
  background-color: #0746f54d;
}

.changeTrack_grid_outer_detail {
  scrollbar-color: blue;
  overflow-y: scroll;
  max-height: var(--changeTrack_max_height);
}

.changeTrack_grid_detail {
  display: grid;
  grid-template-columns: 4.6fr 2.5fr 1fr var(--changeTrack_scroll_width);
  column-gap: 1rem;
  justify-content: center;
  font-size: 0.7rem;
  color: black;
  align-items: center;
  border-bottom: 1px solid black;
}
