.itemTotal_GridContainer {
  display: grid;
  grid-template-columns: 1fr;
  max-height: 307px;
  overflow: auto;
}

.itemTotal_Header {
  display: grid;
  text-align: left;
  grid-template-columns: 1fr 1.5fr 0.5fr 0.5fr 0.5fr 0.5fr 1rem;
  font-size: 0.9rem;
  background-color: rgba(129, 160, 250, 255);
  border-bottom: 1px solid white;
  font-weight: bold;
}

.itemTotal_Header div:nth-child(1) {
  padding-left: 4px;
}

.itemTotal_Header div:nth-child(2) {
  text-align: left;
}

.itemTotal_Header div:nth-child(3),
.itemTotal_Header div:nth-child(4),
.itemTotal_Header div:nth-child(5) {
  text-align: center;
}

.itemTotal_Header div:nth-child(6) {
  text-align: right;
}

.itemTotal_undo_color {
  margin-left: 5px;
  color: blue;
  font-weight: bold;
}

.itemTotal_refresh_color {
  color: blue;
  font-weight: bold;
}

.itemTotal_Detail {
  display: grid;
  text-align: left;
  grid-template-columns: 1fr 1.5fr 0.5fr 0.5fr 0.5fr 0.6fr;
  border-bottom: 1px solid white;
  font-size: 0.9rem;
  background-color: #0746f54d;
}

.itemTotal_Detail div:nth-child(1) {
  padding-left: 4px;
}

.itemTotal_Detail div:nth-child(2) {
  text-align: left;
}

.itemTotal_Detail div:nth-child(3) {
  text-align: center;
}

.itemTotal_Detail div:nth-child(4) {
  text-align: center;
}

.itemTotal_Detail div:nth-child(5) {
  text-align: center;
}

.itemTotal_Detail div:nth-child(6) {
  text-align: center;
}

.itemTotal_Section {
  display: grid;
  grid-template-columns: 3fr 4fr 3fr 3fr 0.5fr;
  grid-template-rows: 1fr;
  align-items: left;
  margin-top: 1rem;
  font-weight: bold;
}

.itemTotal_Section div:nth-child(1) {
  padding-left: 1px;
}

.itemTotal_Section div:nth-child(3),
.itemTotal_Section div:nth-child(4),
.itemTotal_Section div:nth-child(5) {
  text-align: right;
}

.itemTotal_Section input {
  /* background-color: #e9b26a; */
  margin: 3px;
  padding: 1px;
  width: 10rem;
  font-size: 1rem;
  border: 1px solid blue;
  text-align: left;
}

.itemTotal_Section .fa-search {
  color: blue;
}

.itemTotal_Total {
  display: grid;
  text-align: center;
  grid-template-columns: 1fr 1.5fr 0.5fr 0.5fr 0.5fr 0.5fr 1rem;
  font-size: 0.9rem;
  background-color: rgba(129, 160, 250, 255);
  border-bottom: 1px solid white;
  font-weight: bold;
}

.itemTotal_Total div:nth-child(2) {
  padding-left: 4px;
}

.itemTotal_Total div:nth-child(3),
.itemTotal_Total div:nth-child(4),
.itemTotal_Total div:nth-child(5) {
  margin-right: 1rem;
}

.itemTotal_Detail input {
  /* background-color: #e9b26a; */
  margin: 0px;
  padding: 0px;
  padding-left: 0.5rem;
  font-size: 0.8rem;
  border: 1px solid blue;
  text-align: center;
}

.itemTotal_Total .highlightSection {
  color: white;
  background-color: red;
}

.itemTable_delete_btn {
  background-color: red;
}

.itemTotal_Header2 {
  display: grid;
  text-align: center;
  grid-template-columns: 1fr 1.5fr 0.5fr 0.5fr 0.5fr 0.5fr 1rem;
  font-size: 0.9rem;
  background-color: rgba(129, 160, 250, 255);
  border-bottom: 1px solid white;
  font-weight: bold;
}

.itemTotal_Header2 div:nth-child(1) {
  text-align: left;
}

.itemTotal_Header2 div:nth-child(2) {
  text-align: left;
}
.itemTotal_Header2 div:nth-child(3),
.itemTotal_Header2 div:nth-child(4) {
  text-align: center;
}
