.showSharePointFiles_section {
  margin-top: 0rem;
}

.showSharePointFiles_grid_header {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 0.5fr 0.5fr 0.5fr 0.5fr;
  column-gap: 1rem;
  font-size: 0.8rem;
  color: black;
  align-items: center;
  background-color: #0746f54d;
  border: 1px solid black;
  border-collapse: collapse;
}

.showSharePointFiles_grid_detail {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 0.5fr 0.5fr 0.5fr 0.5fr;
  column-gap: 1rem;
  font-size: 0.8rem;
  color: black;
  align-items: center;
  border: 1px solid black;
  border-collapse: collapse;
}

/* except first column all with border left  */
.showSharePointFiles_grid_header div:not(:first-child),
.showSharePointFiles_grid_detail div:not(:first-child) {
  border-left: 1px solid black;
  margin-top: 1px;
}

.showSharePointFiles_grid_header :nth-child(1),
.showSharePointFiles_grid_header :nth-child(2),
.showSharePointFiles_grid_header :nth-child(3) {
  padding-left: 2px;
}

.showSharePointFiles_grid_header :nth-child(4),
.showSharePointFiles_grid_header :nth-child(5),
.showSharePointFiles_grid_header :nth-child(6),
.showSharePointFiles_grid_header :nth-child(7) {
  text-align: center;
}

/* All columns with padding  */
.showSharePointFiles_grid_detail div {
  padding-left: 2px;
}

.showSharePointFiles_grid_detail :nth-child(4),
.showSharePointFiles_grid_detail :nth-child(5),
.showSharePointFiles_grid_detail :nth-child(6),
.showSharePointFiles_grid_detail :nth-child(7) {
  text-align: center;
}

.showSharePointFiles_grid_detail :nth-child(1),
.showSharePointFiles_grid_detail :nth-child(2) {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.showSharePointFiles_grid_detail :hover:nth-child(1),
.showSharePointFiles_grid_detail :nth-child(2) {
  overflow-x: visible;
  text-overflow: inherit;
}

.showSharePoint_grid_large_item {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.showSharePoint_grid_large_item:hover {
  overflow-x: visible;
  text-overflow: inherit;
}

.sharePointFiles_no_files {
  color: black;
  font-size: 0.9rem;
  font-weight: bold;
}

.sharePointFiles_photoBlock img {
  border-radius: 0px; /* Rounded border */
  padding: 1px; /* Some padding */
  width: 15px; /* Set a small width */
  height: 15px; /* Set a small width */
  margin: 0px;
  justify-content: center;
  /*transition: transform  0.1s; /* Animation */
}

.sharePointFiles_photoBlock :hover {
  border-radius: 1px; /* Rounded border */
  padding: 1px; /* Some padding */
  margin: 0px;
  transform: scale(30) translate(-100%, -100%);
}

#sharePointFiles_deleteButton {
  text-align: center;
  font-size: 0.7rem;
  margin: 2px;
  padding: 1px;
}
