.uploadResult_section {
  margin-top: 0rem;
}

.uploadResult_grid_header {
  display: grid;
  grid-template-columns: 2fr 1fr 4fr;
  column-gap: 1rem;
  font-size: 0.8rem;
  color: black;
  align-items: center;
  background-color: #0746f54d;
  border: 1px solid black;
  border-collapse: collapse;  
}

.uploadResult_grid_detail {
  display: grid;
  grid-template-columns:  2fr 1fr 4fr;
  column-gap: 1rem;
  font-size: 0.8rem;
  color: black;
  align-items: center;
  border: 1px solid black;
  border-collapse: collapse;
  
}

.uploadResult_grid_header div:not(:first-child),
.uploadResult_grid_detail div:not(:first-child) {
  border-left: 1px solid black;
  margin-top: 1px;
}
  
.uploadResult_grid_header :nth-child(1),
.uploadResult_grid_header :nth-child(2),
.uploadResult_grid_header :nth-child(3),
.uploadResult_grid_detail :nth-child(1),
.uploadResult_grid_detail :nth-child(2),
.uploadResult_grid_detail :nth-child(3)
{
  padding-left: 2px;
}

.uploadResult_red {
  font-weight: bold;
  color:red;

}