.checkList_select {
  border: 0;
  width: 100%;
  /* background-color: yellow; */
}

.checkList__option {
  width: 100px;
  font-size: 15px;
}

.button-delete:hover {
  background-color: red;
}

.checkFormGrid {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
}

.checkFormGridCell {
  margin-left: 0.8rem;
}

.checkCellSeparation {
  background-color: white;
  color: black;
  /* font-weight: bold; */
}

.checkCellSeparationHeader {
  background-color: #0746f54d;
  color: black;
  text-align: center;
}

.checkCellResult {
  background-color: #eef2f74d;
  color: black;
  /* font-weight: bold; */
}

#checkListTable {
  font-family: Arial, Helvetica, sans-serif;
  border-collapse: collapse;
  /* width:50% */
}

#checkListTable td,
#checkListTable th {
  border: 1px solid #ddd;
  padding: 4px;
  font-size: 0.9rem;
}

#checkListTable tr {
  /* background-color: #e9b26a; */
  background-color: #0746f54d;
  font-size: 1rem;
  font: bold;
}

#checkListTable th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #0746f54d;
  color: black;
}

.checkList_header {
  display: flex;
  margin-top: 5px;
  font-weight: bold;
  font-size: 1.1rem;
  align-items: center;
}
