.app_header {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  background-color: rgb(92, 152, 241);
  color: white;
  height: 3rem;
  font-weight: 800;
  margin-bottom: 0.05rem;
}

ul {
  list-style-type: none;
}

.link {
  color: #fff;
}
.link:hover {
  color: #00f;
}

.welcome {
  display: grid;
  grid-template-columns: 1fr;
  text-align: center;
  border: 0px;
  margin: 0px;
  margin-top: 0vh;
  height: 70vh;
  padding: 0px;
  background-color: rgb(92, 152, 241);
}

.clickable:hover {
  cursor: pointer;
}
