.xDaysResult {
  margin-top: 0.5rem;
  background-color: #0746f54d;
}

.xDaysResult_gridHeader {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 2fr 1fr 1fr 3fr 0.5fr 0.1rem;
  grid-template-rows: 1.5rem;
  column-gap: 1rem;
  font-weight: bold;
  padding-left: 5px;
  align-items: center;
}


.xDaysResult_gridHeaderItem:nth-child(5),
.xDaysResult_gridHeaderItem:nth-child(6)
{  
  margin-left:0.6rem;
}

.xDaysResult_gridHeaderItem:nth-child(7) {  
  text-align:center;  
}


.xDaysResult_gridDetail {
  display: grid;
  grid-template-columns: 2fr 1fr 1fr 2fr 1fr 1fr 3fr 0.5fr 0.1rem;
  grid-template-rows: 1.5rem;
  column-gap: 1rem;
  border-top: 1px solid white;
  padding-left: 5px;
  align-items: center;
  
}

.xDaysResult_gridItem:nth-child(9)  {
  text-align:center  
}


.xDaysResult_gridItem > span {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.xDaysResult_gridItem > span:hover {
  overflow-x: visible;
  text-overflow: inherit;
}

.xDaysResult_gridSelection {
  display: grid;
  grid-template-columns: 1fr   1fr ;
  grid-template-rows: 1.5rem;
  column-gap: 1rem;
  border-top: 1px solid white;
  padding-left: 5px;
  align-items: center;
  border-bottom: 10px solid white;    
}

.xDaysResult_gridSelection_radio {
  display: grid;
  grid-template-columns: 1fr  1fr ;
  grid-template-rows: 1.5rem;
  column-gap: 1rem;
  border-top: 1px solid white;
  padding-left: 5px;
  align-items: center;  
}
