.mfrSelection {
  margin-top: 1px;
  margin-bottom: 1px;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 2rem;
  align-items: center;
  background-color: #0746f54d;
  font-size: 0.75rem;
  font-weight: bold;
  color: blue;
}

.mfrSelection .mfrSelectionSU {
  text-align: left;
  padding-left: 5px;
}

.mfrSelection .mfrSelectionMF {
  text-align: right;
  padding-right: 5px;
}

.mfrSelection_capitalize {
  text-transform: capitalize;
}

.mfrSelection button {
  font-size: 0.9rem;
  margin-left: 0.5rem;
}

.mfrSelection:hover {
  background-color: rgb(92, 152, 241);
}

.mfrSelection_header {
  font-size: 1.2rem;
  color: rgb(142, 172, 218);
  background-color: black;
  padding-left: 0.4rem;
}

.mfrSelection_input input[type="text"] {
  font-size: 0.9rem;
  height: 1.2rem;
  color: black;
  border: 5px solid rgb(92, 152, 241);
  background-color: white;
}
