.defectOption {
  display: grid;
  grid-template-columns: 3fr 5fr 8fr 1.5fr 1.5fr 1fr 1rem;
  grid-template-rows: 3rem;
  column-gap: 1rem;
  align-items: center;
  color: black;
  background-color: rgb(92, 152, 241);
  padding-left: 5px;
}

.defectOption input,
.defectOption select {
  margin: 0px;
  padding: 5px;
  text-align: left;
  border-color: white;
  background-color: white;
  height: 1.7rem;
  font-size: 0.9rem;
}

.defectOption_language {
  color: rgb(233, 70, 21);
  font-weight: bold;
  padding: 0.4rem;
  text-align: left;
}

.defectOption_header {
  display: flex;
  margin-top: 5px;
  margin-bottom: 5px;
  font-weight: bold;
  font-size: 1.1rem;
  align-items: center;
}

.defectOption_product input,
.defectOption_product select {
  font-size: 1rem;
}

.defectOption_defectArea input,
.defectOption_defectArea select {
  font-size: 1rem;
}

.defectOption_defectDetail input,
.defectOption_defectDetail select {
  padding-right: 1.5rem;
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}

.defectOption_major input,
.defectOption_major select {
  margin-left: 5px;
  text-align: center;
  height: 1.7rem;
}

.defectOption_minor input,
.defectOption_minor select {
  text-align: center;
  height: 1.7rem;
}
