
.sharePoint_section {
  font-weight: bold;

}

.sharePoint_photo {
  border-radius: 4px; /* Rounded border */  
  width: 9rem; /* Set a small width */
  height: 3rem; /* Set a small width */
  margin-top:0.5rem;
  margin-left: 1rem;
}


.sharePoint_grid {
  display: grid;
  grid-template-columns: 1fr 0.5fr 2fr 3fr 7fr;
  margin: 0px;
  height: 4rem;
  background-color: rgba(15,112,193,255);
  align-items: center;
  color:white;
  justify-self: left; 
}

.sharePoint_grid .sharePoint_cell:nth-child(5) {  
  text-align: right;
}  
