.fa-check-circle {
  background-color: white;
  color: lightblue;
}

.fa-check-circle:hover {
  background-color: white;
  color: blue;
}

.searchedMC {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1.5rem 1.5rem;
  grid-template-areas:
    "header1 header2 header3"
    ". . .";
  grid-auto-rows: 1.5rem;
  font-size: 0.9rem;

  margin-top: 15px;
  margin-bottom: 15px;
}

.searchedMC_header1 {
  grid-area: header1;
}
.searchedMC_header2 {
  grid-area: header2;
}
.searchedMC_header3 {
  grid-area: header3;
}
.searchedMC_header {
  display: grid;
  grid-template-columns: 5px 1fr 1fr 1fr;
  font-weight: bold;
  background-color: rgba(129, 160, 250, 255);
}

.searchedMC_header:nth-child(2) {
  margin-left: 1px;
  margin-right: 1px;
}

.searchedMC_detail {
  display: grid;
  grid-template-columns: 5px 1fr 1fr 1fr;
  align-items: center;
  font-size: 0.9rem;
  background-color: #0746f54d;
  margin-bottom: 1px;
}

.searchedMC_detail_center {
  text-align: center;
}

.searchedMC_detail:nth-child(3n + 2) {
  margin-left: 1px;
  margin-right: 1px;
}

.searchedMC_detail:hover {
  cursor: pointer;
  font-weight: bold;
  border: 2px solid rgba(129, 160, 250, 255);
}
