.supplierInfo {
  margin-top: 5px;
  margin-bottom: 5px;
  display: grid;
  grid-template-columns: 1fr 5fr 5fr;
  height: 40px;
  align-items: center;
  background-color: #0746f54d;
  font-size: 0.9rem;
  font-weight: bold;
  color: blue;
}

.supplierInfo .supplierInfoSU {
  text-align: left;
  padding-left: 5px;
}

.supplierInfo .supplierInfoMF {
  text-align: right;
  padding-right: 5px;
}

.supplierInfo_capitalize {
  text-transform: capitalize;
}

.supplierInfo button {
  font-size: 0.9rem;
  margin-left: 0.5rem;
}
