.itemTable_GridContainer {
  display: grid;
  grid-template-columns: 1fr;
  max-height: 307px;
  scrollbar-color: green;
  overflow: auto;
}

.itemTable_Header {
  display: grid;
  text-align: center;
  grid-template-columns: 1.5fr 0.5fr 1fr 1fr 1fr 1fr;
  font-size: 0.9rem;
  background-color: rgba(129, 160, 250, 255);
  border-bottom: 1px solid white;
  font-weight: bold;
}

.itemTable_Header div:nth-child(1) {
  padding-left: 4px;
  text-align: left;
}
.itemTable_Header div:nth-child(2) {
  text-align: left;
}
.itemTable_Header div:nth-child(3) {
  text-align: left;
}

.itemTable_Detail {
  display: grid;
  text-align: center;
  grid-template-columns: 1.5fr 0.5fr 1fr 1fr 1fr 1fr;
  border-bottom: 1px solid white;
  font-size: 0.9rem;
  background-color: #0746f54d;
}

.itemTable_Detail div:nth-child(1) {
  padding-left: 4px;
  text-align: left;
}

.itemTable_Detail div:nth-child(2) {
  text-align: left;
}

.itemTable_Detail div:nth-child(3) {
  text-align: center;
}

.itemTable_GridContainer input {
  /* background-color: #e9b26a; */
  padding: 0;
  padding-right: 3px;
  font-size: 0.8rem;
  /* font: bold; */
  margin-top: 3px;
  margin-bottom: 0px;
  border: 1px solid white;
  width: 4rem;
  text-align: right;
}

.itemTable_Section {
  display: flex;
  align-items: center;
  margin-top: 1em;
  font-weight: bold;
  font-size: 1rem;
}

.itemTable_Section div:nth-child(1) {
  padding-left: 2px;
}

.itemTable_Section input {
  /* background-color: #e9b26a; */
  margin: 3px;
  padding: 2px;
  width: 15rem;
  font-size: 1rem;
  border: 1px solid blue;
  text-align: left;
}

.itemTable_Section .fa-plus {
  color: blue;
}

.itemTable_2col {
  display: grid;
  grid-template-columns: 6fr 2rem 7fr;
}
